  text-align: center; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem;
  text-align: center; }

.col-left {
  background-color: #1E213A;
  position: relative; }
  .col-left:after {
    content: "";
    height: 100%;
    width: 100%;
    background-image: url(./../../src/images/Cloud-background.png);
    opacity: .5;
    position: absolute;
    background-repeat: no-repeat;
    left: 0;
    background-position: top;
    top: 50px; }
  .col-left .btn-search-location {
    background-color: #6E707A;
    color: #E7E7EB; }
    .col-left .btn-search-location:hover {
      background-color: #E7E7EB;
      color: #100E1D; }
  .col-left .btn-current-location {
    background-color: #6E707A;
    height: 2.2rem;
    width: 2.2rem; }
    .col-left .btn-current-location:hover {
      background-color: #E7E7EB; }
      .col-left .btn-current-location:hover:before {
        color: #100E1D; }
    .col-left .btn-current-location:before {
      font-family: "Font Awesome 5 Free";
      content: "\f05b";
      font-weight: 900;
      color: #E7E7EB; }
  .col-left .current-temperature {
    font-size: 14.4rem; }
    .col-left .current-temperature sub {
      font-size: 5rem; }
  .col-left .current-phenomenom-atmospheric {
    font-size: 3.6rem; }
  .col-left .date,
  .col-left .current-location {
    margin: 0 1rem;
    font-size: 1.8rem; }
  .col-left .current-location:before {
    font-family: "Font Awesome 5 Free";
    content: "\f3c5";
    font-weight: 900;
    margin-right: 1rem;
    color: #6E707A; }
  .col-left .sunset:before, .col-left .sunrise:before {
    content: "";
    width: 3rem;
    height: 3rem;
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    display: inline-block;
    vertical-align: bottom;
    margin-right: 1rem; }
  .col-left .sunset:before {
    background-image: url(../../src/images/sunset.png); }
  .col-left .sunrise:before {
    background-image: url(../..//src/images/sunrise.png); }

.content {
  padding: 3rem; }
  @media (min-width: 768px) {
    .content {
      padding: 3rem 10rem; } }
  .content h6 {
    font-size: 1.4rem; }
  .content .btn-type-term {
    background-color: #585676;
    color: #E7E7EB;
    font-weight: 700;
    height: 4rem;
    width: 4rem;
    font-size: 1.8rem; }
    .content .btn-type-term:hover, .content .btn-type-term.active {
      background-color: #E7E7EB;
      color: #100E1D; }
  .content .forecasts {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(1, 1fr);
    margin-bottom: 5rem; }
    @media (min-width: 768px) {
      .content .forecasts {
        display: grid;
        gap: 2rem;
        grid-template-columns: repeat(5, 1fr); } }
    .content .forecasts .forecast {
      background-color: #1E213A; }
      .content .forecasts .forecast figure {
        width: 5.6rem;
        height: 6.2rem; }
        .content .forecasts .forecast figure img {
          width: 100%;
          height: 100%;
          object-fit: cover; }
  .content .hightlights {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(1, 1fr); }
    @media (min-width: 768px) {
      .content .hightlights {
        display: grid;
        gap: 4rem;
        grid-template-columns: repeat(2, 1fr); } }
    .content .hightlights .hightlight {
      height: 135px;
      background-color: #1E213A; }
      .content .hightlights .hightlight .wind-speed, .content .hightlights .hightlight .sea-level, .content .hightlights .hightlight .presure {
        font-size: 3.5rem; }
      .content .hightlights .hightlight .wind-direction {
        font-size: 1.2rem; }
        .content .hightlights .hightlight .wind-direction span {
          width: 2.9rem;
          height: 2.9rem;
          background-color: #A09FB1; }
      .content .hightlights .hightlight .wrapper-progress {
        border-radius: 20px;
        background-color: #E7E7EB; }
        .content .hightlights .hightlight .wrapper-progress .progress-humidity {
          color: #100E1D;
          border-radius: 20px;
          display: flex;
          justify-content: center;
          align-items: center;
          transition: 1s ease;
          height: 20px;
          font-weight: 700;
          background-color: #ffec65; }

.menu {
  background-color: #1E213A;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  width: 100%;
  transition: .3s ease-in-out;
  left: -100%; }
  .menu.visible {
    left: 0; }
  .menu .close:hover {
    color: #E7E7EB; }
  .menu .close:focus {
    outline: none; }
  .menu .search .input-search {
    position: relative; }
    .menu .search .input-search:before {
      font-family: Font Awesome\ 5 Free;
      content: "\f689";
      font-weight: 900;
      position: absolute;
      color: #6e707a;
      font-size: 2rem;
      height: 100%;
      display: flex;
      align-items: center;
      left: 3rem; }
  .menu .search input {
    height: 4.8rem;
    border: 1px solid #6E707A;
    background-color: #1E213A;
    color: #E7E7EB;
    padding-left: 5rem; }
    .menu .search input:focus {
      border: 1px solid #E7E7EB; }
      .menu .search input:focus:before {
        color: #E7E7EB; }
  .menu .search .btn-search {
    background-color: #3C47E9;
    color: #E7E7EB;
    height: 4.8rem;
    font-size: 1.4rem; }
